/*---Reset---*/

*,
*::before,
*::after {
    box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
    margin: 0;
}

ul[role='list'],
ol[role='list'] {
    list-style: none;
}

html:focus-within {
    scroll-behavior: smooth;
}

body {
    min-height: 100vh;
    text-rendering: optimizeSpeed;
    line-height: 1.5;
}

a:not([class]) {
    text-decoration-skip-ink: auto;
}

img,
picture {
    max-width: 100%;
    display: block;
}

input,
button,
textarea,
select {
    font: inherit;
}

@media (prefers-reduced-motion: reduce) {
    html:focus-within {
        scroll-behavior: auto;
    }

    *,
    *::before,
    *::after {
        animation-duration: 0.01ms !important;
        animation-iteration-count: 1 !important;
        transition-duration: 0.01ms !important;
        scroll-behavior: auto !important;
    }
}

/*---CSS---*/


.header {
    background-color: rgb(16, 16, 232);
    display: flex;
    align-items: center;
    justify-content: center;
    height: 54px;
}


.search-form {
    width: 300px;
    height: 34px;
    display: flex;
}

.header_button {
    width: 100px;
    height: 34px;
}

.icon_search {
    stroke: #212121;
}

.gallery {
    display: flex;
    flex-wrap: wrap;
}